import React, { useContext, useState } from 'react';
import styled from 'styled-components';
import { observer } from 'mobx-react';
import { Card, Descriptions } from 'antd';
import Section from '../../../../components/section/section';
import ChildProfileForm from '../../../../components/forms/childProfileForm/childProfileForm';
import { ProfileStoreCtx } from '../../../../store/profileStore';
import { avatars } from '../../../../assets/img/index';

const { Meta } = Card;

const ChildProfileSider = styled.div`
  margin-right: 20px;
`;

const DiscriptionWrapper = styled(Descriptions)`
  width: 75%;
`;

const CardWrapper = styled(Card)`
  width: 150px;
  border: none;
`;

const ChildProfileSection = observer(() => {
  const profileStore = useContext(ProfileStoreCtx);
  const [editingProfile, setEditing] = useState<null | ChildProfile>(null);

  const renderChildrenProfile = (childrenProfile: ChildProfile[]) => {
    return (
      <>
        {childrenProfile.map((childProfile: ChildProfile) => {
          return (
            <Section
              key={childProfile.id}
              flexDirection="row"
              onDelete={() =>
                profileStore.removeChildProfile(childProfile.id as number)}
              onEdit={() => setEditing(childProfile)}
            >
              <ChildProfileSider>
                <CardWrapper
                  cover={
                    <img src={avatars[childProfile.iconId]} alt="avatar" />
                  }
                >
                  <Meta
                    title={childProfile.name}
                  />
                </CardWrapper>
              </ChildProfileSider>

              <DiscriptionWrapper layout="vertical" bordered>
                <Descriptions.Item label="Birthday"> {childProfile.dob} </Descriptions.Item>
                <Descriptions.Item label="Favourite Color"> {childProfile.fColor} </Descriptions.Item>
                <Descriptions.Item label="Favourite video/Show/movie"> {childProfile.fVideo} </Descriptions.Item>
                <Descriptions.Item label="Favourite character/cartoon"> {childProfile.fCartoon} </Descriptions.Item>
                <Descriptions.Item label="Favourite shower temperature"> {childProfile.fTemperature} ℃ </Descriptions.Item>
                <Descriptions.Item label="Average shower duration"> {childProfile.averageShowerTime} mins</Descriptions.Item>
                <Descriptions.Item label="Medical Notes" span={3}>
                  {childProfile.medicalNotes}
                </Descriptions.Item>
              </DiscriptionWrapper>
            </Section>
          );
        })}
        <ChildProfileForm
          editingProfile={editingProfile}
          onEndEditing={() => setEditing(null)}
        />
      </>
    );
  };

  return (
    profileStore.isLogin
      ? renderChildrenProfile(
        profileStore.clientProfile?.children as ChildProfile[]
      )
      : null
  );
});

export default ChildProfileSection;
