import React, { useState, useContext } from 'react';
import { Form, Col, Row, Input } from 'antd';
import {
  UserOutlined,
  LockOutlined
} from '@ant-design/icons';
import { ProfileStoreCtx } from '../../../store/profileStore';
import { 
  FormWrapper,
  ConfirmBtn,
  CancelBtn,
  SignUpBtn,
  FormDrawer
} from '../styled';
import { appSize } from '../../../assets/style/style';

function LoginForm() {
  const [visible, setVisible] = useState(false);
  const profileStore = useContext(ProfileStoreCtx);

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  const onLogin = () => {
    profileStore.login();
    setVisible(false);
  };

  return (
    <FormWrapper>
      <SignUpBtn onClick={showDrawer}>Login</SignUpBtn>
      <FormDrawer
        title="Welcome Back"
        width={`calc(100% - ${appSize.appSiderWidth})`}
        onClose={onClose}
        visible={visible}
        bodyStyle={{ paddingBottom: 80 }}
        footer={
          <div>
            <CancelBtn onClick={onClose} style={{ marginRight: 8 }}>
              Cancel
            </CancelBtn>
            <ConfirmBtn onClick={onLogin} type="primary">
              Login
            </ConfirmBtn>
          </div>
        }
      >
        <Form layout="vertical" hideRequiredMark>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item
                name="client"
                label={<span><UserOutlined /> Client Name</span>}
                rules={[{ required: true, message: 'Please enter client name' }]}
              >
                <Input placeholder="Please enter client name" />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item
                name="password"
                label={<span><LockOutlined /> Password</span>}
                rules={[{ required: true, message: 'Enter your password' }]}
              >
                <Input.Password placeholder="input password" />
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </FormDrawer>
    </FormWrapper>
  );
}

export default LoginForm;
