/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { useContext, useEffect, useState } from 'react';
import { observer } from 'mobx-react';
import { Card, Menu } from 'antd';
import styled from 'styled-components';
import { DefaultBtn } from '../../../../components/forms/styled';
import { avatars } from '../../../../assets/img/index';
import SignUpForm from '../../../../components/forms/signUpForm/signUpForm';
import LoginForm from '../../../../components/forms/loginForm/loginForm';
import { ProfileStoreCtx } from '../../../../store/profileStore';
import './index.less';

const { Meta } = Card;

const LogoutBtn = styled(DefaultBtn)`
`;

interface Props {
  goTo: any;
}

const ProfileSider = observer(({ goTo }: Props) => {
  const profileStore = useContext(ProfileStoreCtx);
  const [index, setIndex] = useState(0);

  useEffect(() => {
    goTo(index);
  }, [goTo, index]);

  const renderLogin = () => {
    return (
      <div className='profile-login'>
        <Card
          cover={
            <img className="logo" src={avatars[0]} alt='avatar' />
          }
        >
          <Meta
            description="Welcome"
          />
          <SignUpForm />
          <LoginForm />
        </Card>
      </div>
    );
  };

  const renderSider = (clientProfile: ClientProfile) => {
    return (
      <div className='profile-sider'>
        <Card
          cover={
            <img src={avatars[clientProfile.iconId]} alt='avatar' />
          }
        >
          <Meta
            title={clientProfile.client}
            description="Welcome"
          />
          <LogoutBtn onClick={profileStore.logout}>Logout</LogoutBtn>
        </Card>

        <Menu
          defaultSelectedKeys={[index.toString()]}
          mode="inline"
        >
          <Menu.Item key="0" onClick={() => setIndex(0)}>Profile</Menu.Item>
          <Menu.Item key="1" onClick={() => setIndex(1)}>Child Profiles</Menu.Item>
        </Menu>
      </div>
    );
  };

  return (
    profileStore.isLogin
      ? renderSider(profileStore.clientProfile as ClientProfile)
      : renderLogin()
  );
});

export default ProfileSider;
