import React from 'react';
import { Slider } from 'antd';
import CONST from '../../assets/const';
import './index.less';

// customize marks on the slider
const marks = {
  [CONST.MIN_TEMP]: {
    style: {
      color: '#91d5ff',
    },
    label: <strong>{CONST.MIN_TEMP}°C</strong>,
  },
  [Math.floor((CONST.MAX_TEMP + CONST.MIN_TEMP) / 2)]: {
    style: {
      color: '#c36b80'
    },
    label: (
      <strong>
        {Math.floor((CONST.MAX_TEMP + CONST.MIN_TEMP) / 2)}°C
      </strong>
    ),
  },
  [CONST.MAX_TEMP]: {
    style: {
      color: '#f50',
    },
    label: <strong>{CONST.MIN_TEMP}°C</strong>,
  },
};

interface TemperatureSliderProps {
  temperature: number;
  setTemperature: (temperature: number) => void;
}

function TemperatureSlider({
  temperature,
  setTemperature
}: TemperatureSliderProps) {
  const onChange = (value: number): void => {
    setTemperature(value);
  };

  const toolTipFormatter = (value?: number | undefined) => {
    return `${value}°C`;
  };

  return (
    <div className='temperature-slider-wapper'>
      <h1 className='temperature-display'>{temperature}°C</h1>
      <Slider
        marks={marks}
        value={temperature}
        min={CONST.MIN_TEMP}
        max={CONST.MAX_TEMP}
        onChange={onChange}
        tipFormatter={toolTipFormatter}
      />
    </div>
  );
}

export default TemperatureSlider;
