import React from 'react';
import styled from 'styled-components';
import { Layout } from 'antd';
import { BasicProps } from 'antd/lib/layout/layout';
import { useTimer } from '../../hooks/index';
import { formatDate } from '../../utils/index';
import { appColor, appSize } from '../../assets/style/style';

const Header = styled(Layout.Header)`
  color: white;
  width: 100%;
  margin: auto auto;
  box-shadow: 0 0 10px #000000;
  font-weight: 800;
  background-color: ${appColor.mainColor};
  display: flex;
  height: ${appSize.headerHeight};
  .app-clock {}
  .app-title {
    flex-grow: 1;
    color: white;
    text-align: center;
    font-size: 1.25rem;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 500;
    line-height: 1.6;
    letter-spacing: 0.0075rem;
    margin-block-start: 1rem;
  }
`;

function AppHeader(props: BasicProps) {
  const date = new Date();
  const timer = useTimer(date);

  return (
    <Header {...props}>
      <p className="app-clock">{formatDate(date)}</p>
      <h6 className="app-title"> Smart Shower </h6>
      <p className="app-clock">{timer.toLocaleTimeString()}</p>
    </Header>
  );
}

export default AppHeader;
