import React, { useState } from 'react';
import { Form, Col, Row, Input } from 'antd';
import {
  UserOutlined,
  LockOutlined,
  CameraOutlined
} from '@ant-design/icons';
import AvatarPicker from '../../avatarPicker/avatarPicker';
import {
  FormWrapper,
  ConfirmBtn,
  CancelBtn,
  LoginBtn,
  FormDrawer
} from '../styled';
import { appSize } from '../../../assets/style/style';

function SignUpForm() {
  const [visible, setVisible] = useState(false);

  const showDrawer = () => {
    setVisible(true);
  };

  const onClose = () => {
    setVisible(false);
  };

  return (
    <FormWrapper>
      <LoginBtn onClick={showDrawer}> Sign Up</LoginBtn>
      <FormDrawer
        title="Create a new account"
        width={`calc(100% - ${appSize.appSiderWidth})`}
        onClose={onClose}
        visible={visible}
        bodyStyle={{ paddingBottom: 80 }}
        footer={
          <div>
            <CancelBtn onClick={onClose} style={{ marginRight: 8 }}>
              Cancel
            </CancelBtn>
            <ConfirmBtn onClick={onClose} type="primary">
              Sign Up
            </ConfirmBtn>
          </div>
        }
      >
        <Form layout="vertical" hideRequiredMark>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item
                name="client"
                label={<span><UserOutlined /> Client Name</span>}
                rules={[{ required: true, message: 'Please enter client name' }]}
              >
                <Input placeholder="Please enter client name" />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={48}>
            <Col style={{ flex: 'unset', width: '100%', textAlign: 'left' }}>
              <Form.Item
                name="icon"
                label={<span><CameraOutlined /> Choose Your Icon</span>}
                rules={[{ required: true, message: 'Please choose icon' }]}
              >
                <AvatarPicker />
              </Form.Item>
            </Col>
          </Row>
          <Row gutter={16}>
            <Col span={24}>
              <Form.Item
                name="password"
                label={<span><LockOutlined /> Password</span>}
                rules={[{ required: true, message: 'Enter your password' }]}
              >
                <Input.Password placeholder="input password" />
              </Form.Item>
            </Col>
            <Col span={24}>
              <Form.Item
                name="recheckPassword"
                label={<span><LockOutlined /> Confirm Password</span>}
                rules={[{ required: true, message: 'Enter your password again' }]}
              >
                <Input.Password placeholder="input password again" />
              </Form.Item>
            </Col>
          </Row>
        </Form>
      </FormDrawer>
    </FormWrapper>
  );
}

export default SignUpForm;
