/* eslint-disable @typescript-eslint/no-explicit-any */
import React, { useState } from 'react';
import { Radio } from 'antd';
import styled from 'styled-components';
import { avatars } from '../../assets/img/index';

const AvatarPickerWrapper = styled.div`
  width: 100%;
  .ant-radio-button-wrapper {
    border: 0px;
    
  }
  .ant-radio-button-wrapper:not(:first-child)::before {
    background-color: unset;
  }
`;

const RadioImg = styled.img`
  height: 100%;
  width: 100%;
  opacity: 0.5;
  transition: all 0.2s linear;
`;

const RadioImgSelected = styled(RadioImg)`
  opacity: 1;
`;

const RadioButton = styled(Radio.Button)`
  height: 0;
  padding-bottom: 14%;
  width: 14%;
  margin: 1%;
  span {
    position: absolute;
    top: 0;
    left: 0;
    padding: 0;
  }
`;

// props are injected from Antd Form
function AvatarPicker(props: any) {
  const [selectedValue, setSelectedValue] = useState(0);

  const onClick = (e: React.MouseEvent<HTMLInputElement>) => {
    setSelectedValue(parseInt(e.currentTarget.value, 10));
  };

  return (
    <AvatarPickerWrapper>
      <Radio.Group {...props}>
        {avatars.map((avatar, i) => {
          return (
            <RadioButton 
              key={avatar} 
              value={i}
              onClick={onClick}
            >
              {selectedValue === i
                ? <RadioImgSelected src={avatar} />
                : <RadioImg src={avatar} />}
            </RadioButton>
          );
        })}
      </Radio.Group>
    </AvatarPickerWrapper>
  );
}

export default AvatarPicker;
